@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.text-container {
  position: relative;
  overflow: hidden;
}

.animated-text {
  position: relative;
  bottom: 0;
  opacity: 0;
  -webkit-animation: slide-up 1.5s ease forwards;
          animation: slide-up 1.5s ease forwards;
}

@-webkit-keyframes slide-up {
  to {
    bottom: 0;
    opacity: 1;
  }
}

@keyframes slide-up {
  to {
    bottom: 0;
    opacity: 1;
  }
}


.inline {
  display: flex;
}

.img-single {
  width: 40vw;
  margin: 1vw;
}

@-webkit-keyframes floatUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes floatUp {
  0% {
    opacity: 0;
    -webkit-transform: translateY(50px);
            transform: translateY(50px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.floating-text {
  -webkit-animation: floatUp 2s ease-in-out;
          animation: floatUp 2s ease-in-out;
}




